'use strict';

function _classCallCheck(instance, Constructor) { if (!(instance instanceof Constructor)) { throw new TypeError("Cannot call a class as a function"); } }

var MainController = function MainController($http, $translate, tmhDynamicLocale) {
  'ngInject';
  // Preload meta for the next step

  _classCallCheck(this, MainController);

  $http.get('data/meta.json', { cache: true }).then(function (r) {
    return r.data;
  });
  // Change locale once
  tmhDynamicLocale.set($translate.use().slice(0, 2));
};

angular.module('app').component('main', {
  templateUrl: 'app/main/main.html',
  controller: MainController
});